import 'jest';
import { fixPath } from '../module-registration/module-registration-helper';

describe('fixPath', () => {
    it('can replaces backslashes correctly', () => {
        const input = 'C:\\a\\b\\c';
        const output = 'C:/a/b/c';
        expect(fixPath(input)).toBe(output);
    });
    it('can replaces backslashes correctly when explicitly requested', () => {
        const input = 'C:\\a\\b\\c';
        const output = 'C:/a/b/c';
        expect(fixPath(input, '/')).toBe(output);
    });
    it('can replaces forward correctly', () => {
        const input = 'C:/a/b/c';
        const output = 'C:\\a\\b\\c';
        expect(fixPath(input, '\\')).toBe(output);
    });
});
